import React from 'react';

const Skills = () => {
    return (
        <>
            {/* <!-- Skills --> */}
            <div className="container">
                <div className="row text-center my-5" id="skills">
                    <h2>My Skills</h2>
                </div>

                <div className="row my-5">
                    <div className="col-md-4 mb-5 text-light text-center">
                        <div className="h-100 p-5 border bg-success shadow">
                            <i className="bi bi-code-square fs-1"></i>
                            <p>REACTJS</p>
                        </div>
                    </div>

                    <div className="col-md-4 mb-5 text-light text-center">
                        <div className="h-100 p-5 bg-success shadow">
                            <i className="bi bi-tv fs-1"></i>
                            <p>HTML and Javascript</p>
                        </div>
                    </div>

                    <div className="col-md-4 mb-5 text-light text-center">
                        <div className="h-100 p-5 bg-success shadow">
                            <i className="bi bi-github fs-1"></i>
                            <p>GIT</p>
                        </div>
                    </div>

                    <div className="col-md-4 mb-5 text-light text-center">
                        <div className="h-100 p-5 bg-success shadow">
                            <i class="bi bi-code fs-1"></i>
                            <p>Express</p>
                        </div>
                    </div>

                    <div className="col-md-4 mb-5 text-light text-center">
                        <div className="h-100 p-5 bg-success shadow">
                            <i class="bi bi-server fs-1"></i>
                            <p>MongoDB</p>
                        </div>
                    </div>

                    <div className="col-md-4 mb-5 text-light text-center">
                        <div className="h-100 p-5 bg-success shadow">
                            <i class="bi bi-hexagon fs-1"></i>
                            <p>NodeJs</p>
                        </div>
                    </div>

                </div>
            </div>

        </>
    );
}

export default Skills;