import React from 'react';
import laptop from './../img/laptop.jpg';

const Work = () => {
    return (
        <>
            {/* <!-- Work --> */}
            <section className="bg-grey">
                <div className="row text-center" id="work">
                    <div className="col-10 m-auto">
                        <h2 className="my-5"> My Work</h2>
                        <img src={laptop} alt="#" className="img-work img-fluid rounded-circle mb-5 shadow" />
                        <p>You can find my work on GIT</p>
                        <a href="https://github.com/sumi9518/" target="_blank" className="btn btn-outline-success mb-5">Git Repository</a>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Work;